'use client';

import { useRef, useEffect, memo } from "react";
import { ChevronDown, X } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Checkbox } from "@/components/ui/checkbox";
import { cn } from "@/lib/utils";

interface CountryOption {
  country: string;
  count: number;
}

interface CountryFilterProps {
  countries: CountryOption[];
  selectedCountries: string[];
  onChange: (countries: string[]) => void;
  open: boolean;
  onOpenChange: (open: boolean) => void;
}

export const CountryFilter = memo(function CountryFilter({
  countries,
  selectedCountries,
  onChange,
  open,
  onOpenChange,
}: CountryFilterProps) {
  const KEEP_OPEN_KEY = 'venue-country-filter-keep-open';
  const ref = useRef<HTMLDivElement>(null);

  console.log(' CountryFilter render:', {
    countriesCount: countries.length,
    selectedCountries,
    open,
    hasOnChange: typeof onChange === 'function'
  });

  // Check if we should restore open state after navigation
  useEffect(() => {
    if (typeof window === 'undefined') return;
    const keepOpen = sessionStorage.getItem(KEEP_OPEN_KEY);
    if (keepOpen) {
      console.log(' Found keep-open flag, reopening dropdown');
      sessionStorage.removeItem(KEEP_OPEN_KEY); // Clear immediately
      onOpenChange(true);
    }
  }, [onOpenChange]);

  // Close on click outside
  useEffect(() => {
    if (!open) return;
    
    function handleClickOutside(event: MouseEvent) {
      const target = event.target as Node;
      
      if (ref.current && !ref.current.contains(target)) {
        console.log(' Click OUTSIDE - closing');
        onOpenChange(false);
      } else {
        console.log(' Click INSIDE - ignoring');
      }
    }
    
    // Use capture phase and add delay to let internal handlers finish first
    const timeoutId = setTimeout(() => {
      document.addEventListener("click", handleClickOutside, { capture: true });
    }, 100);
    
    return () => {
      clearTimeout(timeoutId);
      document.removeEventListener("click", handleClickOutside, { capture: true });
    };
  }, [open, onOpenChange]);

  // Close on scroll outside - but NOT on internal dropdown scroll
  useEffect(() => {
    if (!open) return;

    console.log(' Scroll handler - dropdown is open');

    let scrollTimeout: NodeJS.Timeout | null = null;

    function handleScroll(event: Event) {
      const target = event.target as Node;
      
      console.log(' Scroll event detected:', target);

      // Don't close if scrolling inside the dropdown itself
      if (ref.current && ref.current.contains(target)) {
        console.log(' Scroll inside dropdown - ignoring');
        return;
      }

      // Don't close if the scroll event is from the dropdown's scrollable container
      if (target instanceof HTMLElement && target.classList.contains("overflow-y-auto")) {
        console.log(' Scroll in scrollable container - ignoring');
        return;
      }

      console.log(' Scroll outside - closing dropdown');
      // Small delay to allow touch/click events to complete before closing
      if (scrollTimeout) clearTimeout(scrollTimeout);
      scrollTimeout = setTimeout(() => {
        onOpenChange(false);
      }, 100);
    }

    // TEMPORARILY DISABLED - testing if this is causing the issue
    // document.addEventListener("scroll", handleScroll, true);
    // window.addEventListener("scroll", handleScroll, true);

    return () => {
      // document.removeEventListener("scroll", handleScroll, true);
      // window.removeEventListener("scroll", handleScroll, true);
      if (scrollTimeout) clearTimeout(scrollTimeout);
    };
  }, [open, onOpenChange]);

  const handleToggle = (country: string) => {
    console.log(' CountryFilter handleToggle called:', country);
    console.log(' Current selectedCountries:', selectedCountries);
    console.log(' onChange function exists:', typeof onChange);
    
    // Mark that we want to keep the dropdown open after navigation
    sessionStorage.setItem('venue-country-filter-keep-open', 'true');
    
    const newCountries = selectedCountries.includes(country)
      ? selectedCountries.filter((c) => c !== country)
      : [...selectedCountries, country];
    
    console.log(' About to call onChange with:', newCountries);
    onChange(newCountries);
    console.log(' onChange called');
  };

  const handleClearAll = () => {
    // Mark that we want to keep the dropdown open after clearing
    sessionStorage.setItem('venue-country-filter-keep-open', 'true');
    onChange([]);
  };

  const handleClose = () => {
    // User explicitly closed - clear the keep-open flag
    sessionStorage.removeItem('venue-country-filter-keep-open');
    onOpenChange(false);
  };

  const triggerLabel =
    selectedCountries.length === 0
      ? "All Countries"
      : selectedCountries.length === 1
        ? selectedCountries[0]
        : `${selectedCountries.length} Countries`;

  return (
    <div ref={ref} className="relative">
      <Button
        variant="outline"
        size="sm"
        onClick={() => onOpenChange(!open)}
        className={cn(
          "h-9 w-full gap-1.5 px-3 justify-between text-xs font-normal",
          selectedCountries.length > 0
            ? "border-primary bg-primary/10 text-primary hover:bg-primary/20"
            : "border-input bg-background text-foreground hover:bg-muted hover:border-input",
        )}
      >
        <span className="truncate">{triggerLabel}</span>
        <ChevronDown className={cn("h-4 w-4 shrink-0 opacity-50 transition-transform", open && "rotate-180")} />
      </Button>

      {open && (
        <div
          className="absolute top-full left-0 mt-1 z-50 w-64 rounded-md border bg-popover p-1 shadow-lg"
          onMouseDown={(e) => e.stopPropagation()}
          onClick={(e) => e.stopPropagation()}
        >
          <div className="flex items-center justify-between mb-1">
            {selectedCountries.length > 0 ? (
              <button
                type="button"
                onClick={handleClearAll}
                className="flex items-center gap-1 px-2 py-1.5 rounded-sm hover:bg-muted text-sm text-muted-foreground"
              >
                <X className="h-3 w-3" />
                <span className="md:hidden">Clear</span>
                <span className="hidden md:inline">Clear selection</span>
              </button>
            ) : (
              <span />
            )}
            <button
              type="button"
              onClick={handleClose}
              className="flex items-center gap-1 px-2 py-1.5 rounded-sm hover:bg-muted text-sm text-muted-foreground"
            >
              <X className="h-3 w-3" />
              Close
            </button>
          </div>
          <div className="max-h-64 overflow-y-auto overscroll-contain">
            {countries.map(({ country, count }) => (
              <label
                key={country}
                className="flex items-center gap-2 px-2 py-1.5 rounded-sm hover:bg-muted cursor-pointer text-sm"
                onClick={(e) => {
                  console.log(' Label clicked for:', country);
                  console.log(' Event target:', e.target);
                  console.log(' Event currentTarget:', e.currentTarget);
                }}
              >
                <Checkbox 
                  checked={selectedCountries.includes(country)} 
                  onCheckedChange={(checked) => {
                    console.log(' Checkbox onCheckedChange fired for:', country, 'checked:', checked);
                    handleToggle(country);
                  }}
                />
                <span className="flex-1">{country}</span>
                <span className="text-muted-foreground text-xs">({count})</span>
              </label>
            ))}
          </div>
        </div>
      )}
    </div>
  );
});